	module.exports = {"data":{"markdownRemark":{"html":"<center><h1><strong>Deer Ticks Are a Major Threat To America's Health</h1><strong><center>\nYou used to be able to go for a walk in the woods. Bring the dog a long if you wanted to. Today, going for a walk in the woods with your dog means putting your life at risk. We are not talking about bears, tigers, or lions here. We are talking about disease. Diseases caused by ticks:\n<br>\n<br>\n<center><img src=\"/img/tick.png\" alt=\"picture of a real tick biting skin\">\n<br>\n<center><h1><strong>Tick Caused Disease in the United States are on the Rise</h1></strong></center>\n<center>Across the board, tick caused diseases are on the rise. <a href =\"https://www.cdc.gov/media/releases/2018/p0501-vs-vector-borne.html\">(Source CDC)</a></center>\n<br>\n<center><img src=\"/img/TickDisease.png\" alt=\"cdc graph detailing tick disease explosion\"></center>\n<br>\n<center><h1>Not Just Ticks But Mosquitos, Ticks, and Fleas As Well</h1></center>\n<br>\n<center><img src=\"/img/DiseaseChart.png\" alt=\"cdc graph detailing tick disease explosion\"></center>\n<center><h1>Disease Crisis</h1></center>\n<br>\n<p> \"...until our infestations are managed. “<strong>Zika, West Nile, Lyme, and chikungunya—a growing list of diseases</strong> caused by the bite of an infected mosquito, tick, or flea—have confronted the U.S. in recent years, making a lot of people sick. And <strong>we don’t know what will threaten Americans next</strong>”<br>\n<br></p>\n<p> <strong>-CDC Director Robert R. Redfield, M.D.</strong></p>\n<center><h1>Why We Do What We Do</h1></center>\n The subject is widely studied. <a href=\"https://publichealth.yale.edu/people/durland_fish.profile\">Dr. Durland Fish PhD</a>, Yale professor Epidemiology (Microbial Diseases), is one the worlds' foremost experts on the subject of infestation caused diseases. <a href =\"https://www.cdc.gov/media/releases/2018/p0501-vs-vector-borne.html\">(Source CDC)</a> While biotech companies, the amazing work of the CDC, thought leaders like Dr. Fish are working tirelessly to provide solutions to America's infestations, these solutions will take time. Years if not decades.\n<br>\n<br>\n <strong>Tick Killer is here to help kill your neighborhood's ticks today</strong>","frontmatter":{"title":"Deer Tick Killers","meta_title":"Why We Kill Ticks","meta_description":"Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum. Sed posuere consectetur est at lobortis. Cras mattis consectetur purus sit amet fermentum."}}},"pathContext":{"id":"/Users/jacobduchen/Desktop/tick_killers/gatsby-starter-business/src/pages/about/index.md absPath of file >>> MarkdownRemark"}}

